import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {

  private _defaultImgURL: string = "../assets/img/main/bg.jpg";
  private _defaultSite: object = {
    imgURL: this._defaultImgURL,
    url: '/',
    "content-text": 'example site '
  };
  private _bananaBemSite: object = {
    imgURL: "/assets/sites/1/img/bananaBem.png",
    url: 'https://github.com/artur-lapaev/banana-bem',
    "content-text": 'example site '
  };
  private _testSite: object = {
    imgURL: this._defaultImgURL,
    url: '/assets/sites/2/index.html',
    "content-text": 'example site '
  };

  public _containerOfSites = [this._bananaBemSite, this._testSite, this._defaultSite];

  constructor() { }

  ngOnInit() { }

}



